@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700&display=swap);
:root{
  --font-main: 'Source Sans Pro', sans-serif;
  --color--dimgray: #616161;
  --color--dimgray-rgba: rgb(105, 105, 105, 0.7);
  --color--silver: #A3A3A3;
  --color--lightgray: #D9D9D9;
  --color--deepskyblue:#0AD4FA;
  --color--deepskyblue--rgba:rgba(10,212,250,0.7);
  --color--white:#FFFFFF;
  --color--gradient: linear-gradient(180deg, #ffd900 0%, #ff9100 100%);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
  font-family: 'Source Sans Pro', sans-serif;
  font-family: var(--font-main);
}
body{
  background-color: #D9D9D9;
  background-color: var(--color--lightgray);
}
.app{
  width: 100%;
  height: auto;
}
.main{
  width: 100%;
  height: auto;
}
.header{
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 4.2rem;
    background-color: var(--color--white);
    box-shadow: 0rem 0.7rem 0.5rem 0rem rgba(0, 0, 0, 0.2);
}
.header__nav{
    width: auto;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header__logo{
    width: 3.7em;
    height: 4rem;
}
.header__navbar{
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
    list-style:none;
}
.header__navbar--options{
    margin-left: 5rem;
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 4.8rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
    text-decoration: none;
}
.header__navbar--options:hover{
    padding: 0.5rem 0.7rem;
    border-radius: 3rem;
    background-color: var(--color--lightgray);
    color: var(--color--white);
}
.header__userbox{
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
}
.header__username{
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 4.8rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.header__balancebox{
    width: 12rem;
    height: 4rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: 2.8rem;
    border: 0.3rem solid var(--color--silver);
    border-radius: 2rem;
    background-color: var(--color--lightgray);
}
.header__balancebox--balance{
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 4.8rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.header__balancebox--icon{
    width: 2.5rem;
    margin-top: 0rem;
}
.shop{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.shop__hero{
    width: 96%;
    height: 24rem;
    margin-top: 2rem;
}
.shop__title{
    position: absolute;
    top: 13rem;
    left: 10%;
    font-size: 6.4rem;
    font-family: var(--font-main);
    font-weight: 700;
    line-height: 8rem;
    color: var(--color--white);
}
.shop__pagination{
    width: 100%;
    height: 10rem;
    display: flex;
    justify-content:center;
    align-items: center;
    margin: 1.5rem 0rem;
}
.shop__pagination--text{
    width: 14rem;
    margin-right: 2rem;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.shop__pagination--topbox{
    width: 40rem;
    height: 100%;
}
.shop__products{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2.4rem;
    gap: 2.4rem;
    margin-top: 1rem;
    padding: 0 3rem;
}
.shop__pagination--botbox{
    width: 100%;
    height: 10rem;
}
.filters{
    width: 50rem;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 0.2rem solid var(--color--silver) ;
}
.filters__text{
    font-size: 2.4rem;
    font-weight: 400;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.filters__select{
    width: 14rem;
    height: 4rem;
    margin-left: 2rem;
    padding-left: 0.7rem;
    border: none;
    border-radius: 2rem;
    font-size: 1.6rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
    outline: none;
    cursor: pointer;
}
.filters__select:focus{
    width: 17rem;
    border: 0.3rem solid var(--color--deepskyblue);
    /* background-color: var(--color--deepskyblue);
    color: var(--color--white); */

}
.product{
    width: 27.6rem;
    height: 27.6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 0.5rem;
    background-color: var(--color--white);
    box-shadow: 0.6rem 0.7rem 0.5rem 0rem rgba(0, 0, 0, 0.2);
}
.product__pointsremaining{
    width: 15rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
    border: none;
    border-radius: 10rem;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--white);
    background-color: var(--color--dimgray-rgba);
    z-index: 100;
}
.product__pointsremaining--icon{
    width: 2rem;
    margin-left: 0.3rem;
}
.product__icon{
    width: 4.2rem;
    height: 4.2rem;
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
    z-index: 100;
}
.product__icon--hover{
    width: 4.3rem;
    display: none;
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
    z-index: 200;
}
.product__image{
    width: 25.2rem;
    height: 18.2rem;
}
.product__line{
    width: 22.8rem;
    height: 0.1rem;
    border: none;
    background-color: var(--color--lightgray);
}
.product__category{
    margin-top: 2rem;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--silver);
}
.product__name{
    margin-top: 0.2rem;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.3rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.product__hover{
    width: 100%;
    height: 100%;
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    border-radius: 0.5rem;
    background-color: var(--color--deepskyblue--rgba);
}
.product:hover > .product__hover{
    display: flex;
}
.product:hover > .product__icon--hover{
    display: block;
}
.product__price{
    width: 22rem;
    height: 4.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product__price--value{
    font-size: 3.6rem;
    font-weight: 400;
    line-height: 4.2rem;
    font-family: var(--font-main);
    color: var(--color--white);
}
.product__price--icon{
    width: 3rem;
    margin-left: 1rem;

}
.product__btn{
    width: 22rem;
    height: 4.2rem;
    margin-top: 1.2rem;
    border-radius: 2rem;
    border: none;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.2rem;
    font-family: var(--font-main);
    background: var(--color--white);
    color: var(--color--dimgray);
    cursor: pointer;

}
.product__btn:hover{
    background: var(--color--gradient);
    color: var(--color--white);
}

.product__btn.disable{
    background: var(--color--dimgray);
    color: var(--color--white);
}
.product__btn.disable:hover{
    background: var(--color--gradient);
    color: var(--color--white);
}
.pagination{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.pagination__btn{
    width: 4rem;
    height: 4rem;
    margin-left: 1rem;
    border: 0.2rem solid var(--color--silver);
    border-radius: 50%;
    font-size: 2.8rem;
    font-weight: 400;
    font-family: var(--font-main);
    color: var(--color--dimgray);
    background-color: transparent;
}
.pagination__btn:hover{
    background-color: var(--color--white);
    opacity: 0.7;
}
.pagination__btn.left{
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}
.pagination__btn.right{
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.redeems{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.redeems__title{
    margin-top: 3rem;
    font-size: 3.7rem;
    font-weight: 700;
    line-height: 4.2rem;
    font-family: var(--font-main);
    color: var(--color--deepskyblue);
}
.redeems__subtitle{
    margin-top: 1rem;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.redeems__pagination{
    width: 100%;
    height: 10rem;
}
.redeems__orders{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-top: 1rem;
}
.order{
    width: 60rem;
    height: 17rem;
    min-width: 27rem;
    display: flex;
    margin-top: 2rem;
    border-radius: 1rem;
    border: 0.4rem solid var(--color--dimgray);
    box-shadow: 0rem 0.7rem 0.5rem 0rem rgba(0, 0, 0, 0.2);
}
.order:hover{
    border: 0.4rem solid var(--color--deepskyblue);
}
.order__img{
    width: 30%;
    height: 100%;
    border-radius: 1rem 0 0 1rem;

}
.order__data{
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;

}
.order__data--text{
    font-size: 1.6rem;
    font-weight: 700;
    line-height: 2.8rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.coins{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.coins__title{
    margin-top: 3rem;
    font-size: 3.7rem;
    font-weight: 700;
    line-height: 4.2rem;
    font-family: var(--font-main);
    color: var(--color--deepskyblue);
}
.coins__subtitle{
    margin-top: 1rem;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.coins__container{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 5rem;
}
.coins__container--btn{
    width: 22rem;
    height: 5.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4rem;
    border: 0.3rem solid var(--color--white);
    font-size: 2.8rem;
    font-weight: 700;
    line-height: 2.2rem;
    font-family: var(--font-main);
    background: var(--color--deepskyblue);
    color: var(--color--white);
    cursor: pointer;
    box-shadow: 0rem 0.7rem 0.5rem 0rem rgba(0, 0, 0, 0.2);
}
.coins__container--btn:hover{
    background: var(--color--gradient);
}
.coins__container--img{
    width: 3rem;
    margin-left: 1rem;
}
.background__modalerror{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    background-color: var(--color--dimgray-rgba);
    z-index: 500;
}
.modalerror{
    width: 27.6rem;
    height: 27.6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 0.5rem;
    background: linear-gradient(0deg, #f17e62 40%, #ff0037 100%);
}
.modalerror__gif{
    width: 12rem;
    margin-top: 2rem;
    border: 0.5rem solid var(--color--dimgray);
    border-radius: 50%;
    background-color: var(--color--white);
}
.modalerror__title{
    margin-top: 1rem;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: var(--color--white);
}
.modalerror__text{
    margin-top: 1rem;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.modalerror__btn{
    width: 15rem;
    height: 3rem;
    margin-top: 1rem;
    border: none;
    border-radius: 3rem;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: #f17e62;
    background-color: var(--color--white);
    cursor: pointer;
}
.modalerror__btn:hover{
    background: var(--color--gradient);
}
.background__modalerror.disable{
    display: none;
}
.background__modalsuccess{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    background-color: var(--color--dimgray-rgba);
    z-index: 500;
}
.modalsuccess{
    width: 27.6rem;
    height: 27.6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 0.5rem;
    background: linear-gradient(0deg, #76ee76 40%, #08f308 100%);
}
.modalsuccess__gif{
    width: 12rem;
    margin-top: 2rem;
    border: 0.5rem solid var(--color--dimgray);
    border-radius: 50%;
    background-color: var(--color--white);
}
.modalsuccess__title{
    margin-top: 1rem;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: var(--color--white);
}
.modalsuccess__text{
    margin-top: 1rem;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 2rem;
    font-family: var(--font-main);
    color: var(--color--dimgray);
}
.modalsuccess__btn{
    width: 15rem;
    height: 3rem;
    margin-top: 1rem;
    border: none;
    border-radius: 3rem;
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.2rem;
    font-family: var(--font-main);
    color: #76ee76;
    background-color: var(--color--white);
    cursor: pointer;
}
.modalsuccess__btn:hover{
    background: var(--color--gradient);
}
.background__modalsuccess.disable{
    display: none;
}
